export class AppProviders {
    public static getProviders() {
        let providers;
        if(document.URL.includes('https://') || document.URL.includes('http://')){
          // Use browser providers
          providers = [
						UserService,
						NotificationService,
						//Camera,
						Facebook,
						AuthService,
						UtilService,
						UserService,
						ChatroomService,
						AngularFireDatabase,
						VipService,
						UtilService,
						RequestService,
						Geolocation,
						{provide: ErrorHandler, useClass: IonicErrorHandler}
						/* import services */
          ];
        } else {
          // Use device providers
          providers = [
            UserService,
            NotificationService,
            //Camera,
            Facebook,
            AuthService,
            UtilService,
            UserService,
						ChatroomService,
						AngularFireDatabase,
            VipService,
            UtilService,
						RequestService,
            Geolocation,
            {provide: ErrorHandler, useClass: IonicErrorHandler}
            /* import services */
          ];
        }
        return providers;
    }
}



